* {
  margin: 0;
  box-sizing: border-box;
}

body {
  background: #202027;
  width: 100%;
  height: 100%;
  color: white;
}
.board-section h1 {
  position: relative;
  text-align: center;
  font-size: 64px;
}

.board {
  position: absolute;
  display: inline-block;
  top: 100px;
  left: 50%;
  height: 800px;
  width: 800px;
  overflow: hidden;
  transform: translate(-50%, 0);
}

.circle-overlay {
  position: absolute;
  width: 100px;
  height: 100px;
  overflow: hidden;
  background: rgba(0, 0, 0, 0);
}

.circle-overlay .circle {
  height: 50px;
  width: 50px;
  background-color: green;
  border-radius: 50%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
